import axios from "axios";
import { URL_BACKEND } from "../components/enviroments/enviroments";

export const getTipos = async ()=>{
    const resultado = await axios.get(`${URL_BACKEND}/tipo`);
    return resultado.data;
}

export const postTipo = async (objTipo)=>{
    const resultado = await axios.post(`${URL_BACKEND}/tipo`,
    JSON.stringify(objTipo), {headers: {"Content-type":"application/json"}});
    return resultado.data;
}

export const deleteTipoById = async (id)=>{
    const respuesta = await axios.delete(`${URL_BACKEND}/tipo/${id}`);
    return respuesta.data;
}

export const putTipo = async (objTipo)=>{
    const respuesta = await axios.put(`${URL_BACKEND}/tipo/${objTipo.tipo_id}`,
                    JSON.stringify(objTipo), {headers:{"Content-type":"Application/json"}});
    return respuesta;
}
