import axios from "axios";
import { URL_BACKEND } from "../components/enviroments/enviroments";

export const getRazas = async ()=>{
    const resultado = await axios.get(`${URL_BACKEND}/raza`);
    return resultado.data;
}

export const postRaza = async (objRaza)=>{
    const resultado = await axios.post(`${URL_BACKEND}/raza`,
    JSON.stringify(objRaza), {headers: {"Content-type":"application/json"}});
    return resultado.data;
}

export const deleteRazaById = async (id)=>{
    const respuesta = await axios.delete(`${URL_BACKEND}/raza/${id}`);
    return respuesta.data;
}

export const putRaza = async (objRaza)=>{
    const respuesta = await axios.put(`${URL_BACKEND}/raza/${objRaza.raza_id}`,
                    JSON.stringify(objRaza), {headers:{"Content-type":"Application/json"}});
    return respuesta;
}
