import React from 'react';
import { MDBDataTable } from 'mdbreact';
import { deleteTipoById, putTipo } from './servicesTipos';
import Swal from 'sweetalert2';

const TiposTabla = ({tipos, traerTipos, setTiposEditar}) => {
    const eliminarTipoPorId = (id)=>{
        Swal.fire({
            title: "¿Eliminar?",
            text: "Los cambios serán irreversibles",
            icon: "question",
            showCancelButton: true
        }).then(({isConfirmed})=>{
            if(isConfirmed){
                deleteTipoById(id).then((data)=>{
                    if(data.tipo_id){
                        traerTipos();
                        Swal.fire({
                            title: "Eliminado!",
                            text: "Registro eliminado satisfactoriamente",
                            icon: "success",
                            timer: 2000
                        })
                    }
                });
            }
        })
    }

    const data = {
        columns: [
            {label: "Id", field: "tipo_id",},
            {label: "Nombre Tipo", field: "tipo_nombre",},
            {label: "Acciones", field: "acciones"}
        ],
        rows: tipos.map((t)=>{
            return {
                ...t,
                acciones: (
                    <>
                    <button className="btn btn-primary mr-2" onClick={()=>{setTiposEditar({...t})}}>Editar</button>
                    <button className="btn btn-danger" onClick={()=>{eliminarTipoPorId(t.tipo_id)}}>Eliminar</button>
                    </>
                )
            }
        })
    };

    return (
        <div className="row mt-3">
            <div className="col-12">
                <div className="card shadow">
                    <div className="card-body">
                        <MDBDataTable className="text-center" data={data}
                            responsive striped bordered hover />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TiposTabla
