import React, { useState } from 'react'
import { useEffect } from 'react';
import Swal from "sweetalert2";
import { postTipo, putTipo } from './servicesTipos';

const formularioVacio = {tipo_nombre: ""};

const TiposForm = ({traerTipos, tiposEditar}) => {
    const [formulario, setFormulario] = useState(formularioVacio);
    const [modo, setModo] = useState("crear");

    const handleChange = (e)=>{setFormulario({...formulario, [e.target.name]:e.target.value});}

    const handleSubmit = (e)=>{
        e.preventDefault();

        if(formulario.tipo_nombre.trim()===""){
            Swal.fire({
                title: "Ups!",
                text: "Revisa bien el formulario e inténtalo denuevo",
                icon: "error",
                timer: 2000
            })
            return;
        }

        if(modo==="crear"){
            Swal.fire({
                title: "¿Registrar Nuevo Tipo?",
                text: "Se guardará en la base de datos",
                icon: "question",
                showCancelButton: true,
            }).then(({isConfirmed})=>{
                if(isConfirmed){
                    postTipo(formulario).then((data)=>{
                        if(data.tipo_id){
                            traerTipos();
                            setFormulario(formularioVacio);
                            Swal.fire({
                                title: "Registrado!",
                                text: "Nuevo tipo registrado correctamente",
                                icon: "success",
                                timer: 2000
                            })
                        }
                    })
                }
            })
        } else {
            Swal.fire({
                title: "¿Editar?",
                text: "Seguro que desea editar el registro?",
                icon: "question",
                showCancelButton: true
            }).then(({isConfirmed})=>{
                if(isConfirmed){
                    putTipo({...formulario}).then((respuesta)=>{
                        if(respuesta.status===200){
                            traerTipos();
                            setModo("crear");
                            setFormulario(formularioVacio);
                            Swal.fire({
                                position: "top-end",
                                timer: 1000,
                                title: "Registro actualizado!",
                                showConfirmButton: false
                            })
                        }
                    })
                }
            })
        }
    }

    useEffect(()=>{
        if(tiposEditar){
        setFormulario(tiposEditar);
        setModo("editar");
        }
    }, [tiposEditar])

    return (
        <div className="row">
            <div className="col-12">
                <div className="card shadow mt-3">
                    <div className="card-body bg-dark">
                        <form className="row" onSubmit={handleSubmit}>
                            <div className="form-group col-md-4">
                                <input type="text" className="form-control"
                                    placeholder="Ingrese Tipo"
                                    name="tipo_nombre"
                                    onChange={handleChange}
                                    value={formulario.tipo_nombre} />
                            </div>
                            <div className="form-group col-md-3">
                                <button className="btn btn-block btn-primary" type="submit">
                                    {modo==="crear" ? "Crear Tipo" : "Guardar Cambios"}
                                </button>
                            </div>
                            <div className="form-group col-md-3">
                                <button className="btn btn-block btn-danger" type="button">Cancelar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TiposForm
