import React, { useState } from 'react';
import { useEffect } from 'react';
import Swal from "sweetalert2";
import { postMascota, putMascota } from './services/mascota';

const formularioVacio = {
    mascota_nombre: "",
    mascota_raza: 0,
    mascota_tipo: 0,
    mascota_edad: 0,
    mascota_colores: "",
    mascota_activo: true,
};

const MascotasForm = ({traerMascotas, mascotaEditar}) => {

    const [formulario, setFormulario] = useState(formularioVacio);
    const [modo, setModo] = useState("crear");

    const handleChange = (e)=>{
        let valor = e.target.type === "checkbox" ? e.target.checked : e.target.value;
        setFormulario({...formulario, [e.target.name]:valor});
    }

    const handleSubmit = (e)=>{
        e.preventDefault();

        if(formulario.mascota_nombre.trim()==="" || formulario.mascota_colores.trim()==="" ||
            formulario.mascota_raza===0 || formulario.mascota_tipo===0 ||
            formulario.mascota_edad<=0 || formulario.mascota_activo===false){
                Swal.fire({
                    title: "Ups!",
                    text: "Revisa bien el formulario e inténtalo denuevo",
                    icon: "error",
                    timer: 2500
                })
                return;
            }

        if(modo==="crear"){
            Swal.fire({
                title: "¿Registrar Mascota?",
                text: "Se guardará en la base de datos",
                icon: "question",
                showCancelButton: true,
            }).then(({isConfirmed})=>{
                if(isConfirmed){
                    postMascota(formulario).then((data)=>{
                        if(data.mascota_id){
                            traerMascotas();
                            setFormulario(formularioVacio);
                            Swal.fire({
                                title: "Registrado!",
                                text: "Mascota registrada correctamente",
                                icon: "success",
                                timer: 2000
                            })
                        }
                    })
                }
            })
        } else {
            Swal.fire({
                title: "¿Editar?",
                text: "Seguro que desea editar el registro?",
                icon: "question",
                showCancelButton: true
            }).then(({isConfirmed})=>{
                if(isConfirmed){
                    putMascota({...formulario}).then((respuesta)=>{
                        if(respuesta.status===200){
                            traerMascotas();
                            setModo("crear");
                            setFormulario(formularioVacio);
                            Swal.fire({
                                position: "top-end",
                                timer: 1000,
                                title: "Registro actualizado!",
                                showConfirmButton: false
                            })
                        }
                    })
                }
            })
        }
    }

    useEffect(()=>{
        if(mascotaEditar){
        setFormulario(mascotaEditar);
        setModo("editar");
        }
    }, [mascotaEditar])

    return (
        <div className="row">
            <div className="col-12">
                <div className="card shadow mt-3">
                    <div className="card-body">
                        <form className="row" onSubmit={handleSubmit}>
                            <div className="form-group col-md-6">
                                <label htmlFor="">Nombre:</label>
                                <input type="text" className="form-control"
                                    placeholder="Ingrese nombre" 
                                    name="mascota_nombre"
                                    onChange={handleChange}
                                    value={formulario.mascota_nombre}/>
                            </div>
                            <div className="form-group col-md-6">
                                <label htmlFor="">Raza:</label>
                                <select className="form-control"
                                name="mascota_raza"
                                onChange={handleChange}
                                value={formulario.mascota_raza}>
                                    <option value="0">Seleccione</option>
                                    <option value="1">Bull Terrier</option>
                                </select>
                            </div>
                            <div className="form-group col-md-6">
                                <label htmlFor="">Colores:</label>
                                <input type="text" className="form-control"
                                    placeholder="Ingrese colores"
                                    name="mascota_colores"
                                    onChange={handleChange}
                                    value={formulario.mascota_colores}/>
                            </div>
                            <div className="form-group col-md-6">
                                <label htmlFor="">Edad</label>
                                <input type="number" className="form-control"
                                name="mascota_edad"
                                onChange={handleChange}
                                value={formulario.mascota_edad}/>
                            </div>
                            <div className="form-group col-md-6">
                                <label htmlFor="">Tipo</label>
                                <select className="form-control"
                                name="mascota_tipo"
                                onChange={handleChange}
                                value={formulario.mascota_tipo}>
                                    <option value="0">Seleccione</option>
                                    <option value="1">Perro</option>
                                </select>
                            </div>
                            <div className="form-group col-md-6">
                                <label htmlFor="">¿Registro Activo?:</label>
                                <div className="input-group mb-3">
                                    <div className="input-group-prepend">
                                        <div className="input-group-text">
                                            <input type="checkbox" aria-label="Checkbox for following text input"
                                            name="mascota_activo"
                                            onChange={handleChange}
                                            checked={formulario.mascota_activo}
                                            id="checkActivo"/>
                                        </div>
                                    </div>
                                    <label htmlFor="checkActivo" className="form-control" >Activo</label>
                                </div>
                            </div>
                            <div className="form-group col-md-6">
                                <button className="btn btn-block btn-primary"
                                        type="submit">
                                            {modo==="crear" ? "Crear Mascota" : "Guardar Cambios"}
                                </button>
                            </div>
                            <div className="form-group col-md-6">
                                <button className="btn btn-block btn-danger"
                                        type="button">Cancelar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MascotasForm
