import React from 'react';
import { MDBDataTable } from 'mdbreact';
import { deleteMascotaById, putMascota } from './services/mascota';
import Swal from 'sweetalert2';
import Toggle from "react-toggle";
import "react-toggle/style.css";

const MascotasTabla = ({mascotas, traerMascotas, setMascotaEditar}) => {
    const eliminarMascotaPorId = (id)=>{
        Swal.fire({
            title: "¿Eliminar?",
            text: "Los cambios serán irreversibles",
            icon: "question",
            showCancelButton: true
        }).then(({isConfirmed})=>{
            if(isConfirmed){
                deleteMascotaById(id).then((data)=>{
                    if(data.mascota_id){
                        traerMascotas();
                        Swal.fire({
                            title: "Eliminado!",
                            text: "Registro eliminado satisfactoriamente",
                            icon: "success",
                            timer: 2000
                        })
                    }
                });
            }
        })
    }

    const handleChangeToggle = (estado, objMascota)=>{
        putMascota({...objMascota, mascota_activo: estado}).then(rpta => {
            if(rpta.status===200){
                Swal.fire({
                    position: "top-end",
                    icon: "success",
                    title: "Estado Actualizado",
                    showConfirmButton: false,
                    timer: 1000
                })
            }
        })
    }

    const data = {
        columns: [
            {label: "Id", field: "mascota_id",},
            {label: "Nombre", field: "mascota_nombre",},
            {label: "Colores", field: "mascota_colores",},
            {label: "Raza", field: "mascota_raza",},
            {label: "Tipo", field: "mascota_tipo",},
            {label: "Edad", field: "mascota_edad",},
            {label: "Activo", field: "mascota_activo",},
            {label: "Acciones", field: "acciones"}
        ],
        rows: mascotas.map((m)=>{
            return {...m,
                mascota_activo: <Toggle defaultChecked={m.mascota_activo} onChange={(e)=>{
                                        handleChangeToggle(e.target.checked, {...m})}}/>,
                acciones: (<>
                    <button className="btn btn-primary mr-2" onClick={()=>{setMascotaEditar({...m})}}>Editar</button>
                    <button className="btn btn-danger" onClick={()=>{eliminarMascotaPorId(m.mascota_id)}}>Eliminar</button>
                </>)
            }
        })
    };

    return (
            <div className="row mt-4">
                <div className="col-12">
                    <div className="card shadow">
                        <div className="card-body">
                        <MDBDataTable className="text-center" data={data}
                                    responsive striped bordered hover />
                        </div>
                    </div>
                </div>
            </div>
    )
}

export default MascotasTabla
