import React, { useEffect, useState } from 'react';
import RazasForm from './RazasForm'
import RazasTabla from './RazasTabla'
import { getRazas } from './servicesRazas';

const Razas = () => {
    const [razas, setRazas] = useState([]);
    const [razasEditar, setRazasEditar] = useState(null);

    const traerRazas = async ()=>{
        const data = await getRazas();
        setRazas(data);
    }

    useEffect(()=>{
        traerRazas();
    }, []);

    return (
        <>
        <RazasForm traerRazas={traerRazas} razasEditar={razasEditar}/>
        <RazasTabla razas={razas} traerRazas={traerRazas} setRazasEditar={setRazasEditar}/>
        </>
    )
}

export default Razas
