import React from 'react'
import { MDBDataTable } from 'mdbreact';
import { deleteRazaById, putRaza } from './servicesRazas';
import Swal from 'sweetalert2';

const RazasTabla = ({razas, traerRazas, setRazasEditar}) => {
    const eliminarRazaPorId = (id)=>{
        Swal.fire({
            title: "¿Eliminar?",
            text: "Los cambios serán irreversibles",
            icon: "question",
            showCancelButton: true
        }).then(({isConfirmed})=>{
            if(isConfirmed){
                deleteRazaById(id).then((data)=>{
                    if(data.raza_id){
                        traerRazas();
                        Swal.fire({
                            title: "Eliminado!",
                            text: "Registro eliminado satisfactoriamente",
                            icon: "success",
                            timer: 2000
                        })
                    }
                });
            }
        })
    }

    const data = {
        columns: [
            {label: "Id", field: "raza_id",},
            {label: "Nombre Raza", field: "raza_nombre",},
            {label: "Acciones", field: "acciones"}
        ],
        rows: razas.map((r)=>{
            return {...r,
                acciones: (<>
                    <button className="btn btn-primary mr-2" onClick={()=>{setRazasEditar({...r})}}>Editar</button>
                    <button className="btn btn-danger" onClick={()=>{eliminarRazaPorId(r.raza_id)}}>Eliminar</button>
                </>)
            }
        })
    };

    return (
        <div className="row mt-3">
            <div className="col-12">
                <div className="card shadow">
                    <div className="card-body">
                        <MDBDataTable className="text-center" data={data}
                            responsive striped bordered hover />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default RazasTabla
