import React, { useEffect, useState } from 'react';
import TiposForm from './TiposForm'
import TiposTabla from './TiposTabla'
import { getTipos } from './servicesTipos';

const Tipos = () => {
    const [tipos, setTipos] = useState([]);
    const [tiposEditar, setTiposEditar] = useState(null);
    
    const traerTipos = async ()=>{
        const data = await getTipos();
        setTipos(data);
    }

    useEffect(()=>{
        traerTipos();
    }, []);

    return (
        <>
        <TiposForm traerTipos={traerTipos} tiposEditar={tiposEditar}/>
        <TiposTabla tipos={tipos} traerTipos={traerTipos} setTiposEditar={setTiposEditar}/>
        </>
    )
}

export default Tipos
